
@import url('https://fonts.googleapis.com/css2?family=Cinzel&display=swap');
.header {
  margin-bottom: 40px;
  width: 100vw;
  display: flex;
  justify-content: space-around;
  background-color: rgb(85, 83, 83);
  height: 27px;
}
.inputSearch{
 top: 0;
  opacity: 0.5;
  height: 25px;
  margin-left: 40px;
  border: transparent;
}
.wrapperCard{
  height: 330px;
  width: 180px;
  margin: 20px;
}
.imgPoster{
  width: 100px;
  margin: auto;
}
body{
  max-width: 100vw;
  background-color: rgb(151, 167, 179);
}

.container {
  flex-wrap: wrap;
  display: flex;
  max-width: 90vw;
}
#about{
  height: 100%;
  margin-right: 5px;
  display: inline-block;
}
#home{
  height: 100%;
  margin-right: 5px;
  display: inline-block;
}
#sort {
  height: 100%;
  margin-right: 5px;
  border-radius: 0;
  display: inline-block;
}
#search {
  display: inline-block;
  height: 100%;
border-radius: 0;
  
}

.button {
  
  background-size: 170% auto;
  transition: .3s;
  text-decoration: none;
  background-color: rgb(221, 208, 31);
  font-family: 'Cinzel', serif;
  font-weight: 900;
  color: #000000;
  letter-spacing: 2px;
  padding: 5px;
  text-decoration: none;
  
}
.button:hover{
  background-position: right center;
  text-decoration: none;
  transition: 0.4s;
  background-color: rgb(255, 244, 86);
  box-shadow: 0 0 20px rgba(53, 51, 42, 0.1);
}
h3{
  background-color: rgb(221, 208, 31);
  font-family: 'Cinzel', serif;
  display: inline-block ;
  color: #000000;
  vertical-align:super;
  font-weight: 900;
  font-size: 25px;
  letter-spacing: 6px;
padding-left: 20px;
padding-right: 20px;
height: 100%;
}
